@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{width:1440px;box-sizing:border-box;background-repeat:no-repeat;box-shadow:inset 0px 0px 100px 100px rgba(248,250,253,0.75);padding:0px 140px}.App header{display:flex;justify-content:space-between;align-items:center}.App header nav{padding:27px 0}.App header nav ul{display:flex;margin:0;font-size:16px;color:#6b8cae}.App header nav ul li{padding:0 10px;cursor:pointer}.App header nav ul li::before{content:'';display:block;width:0%;height:1px;background-color:#6b8cae;margin-bottom:5px;-webkit-transition:all ease 0.2s;transition:all ease 0.2s}.App header nav ul li:hover{text-shadow:1px 0 0 #6b8cae}.App header nav ul li:hover::before{width:100%}.App section.impress{padding-top:155px;margin-right:-40px;background:url(/static/media/laptop.0f27710f.webp) 100% 0% no-repeat;min-height:520px;-webkit-animation:wave 5s infinite;animation:wave 5s infinite;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}.App section.impress h1{font-size:48px}.App section.impress .button{margin-top:40px}.App section.impress .button:hover{background-position:-200px 0px}.App section.a4{margin-top:200px;text-align:center}.App section.a4 p{margin:10px 0}.App section.a4 .button{display:inline-block;margin-top:480px}.App section.a5{margin-top:175px;text-align:center}.App section.a5 p{margin:10px 0}.App section.a5 .button-hollow{display:inline-block;margin-top:330px}.App section.a6{margin-top:95px;padding:70px 0;display:flex;align-items:center;border-top:2px solid #dfedfb;border-bottom:2px solid #dfedfb}.App section.a6 h3{flex-grow:1}.App section.a6 .button{margin:0 10px}.App section.a6 .button:last-child{margin-right:0}.App footer nav{display:flex;justify-content:space-between;padding:30px 0 50px 0}.App footer nav ul li{margin-bottom:21px}.App footer nav ul li a:hover{text-shadow:1px 0 0 #6b8cae}.App footer nav .address{padding:0 25px;line-height:1.5;text-align:right}.App footer nav svg.pin{fill:#91b7dc;margin-top:5px}.App footer nav .social-icons{display:inline-flex;justify-content:space-between;width:215px}.App footer .credits{display:flex;justify-content:space-between;align-items:center;height:52px;padding:0 20px;font-size:12px;background-color:#dfedfb}.App footer .credits .credits-link a{color:#6b8cae;text-decoration:underline}hr{border:2px solid #dfedfb}.button{width:211px;height:46px;line-height:46px;text-align:center;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;color:#f8fafd;border-radius:23px;box-shadow:1px 1px 2px #0000004a;background:linear-gradient(45deg, #00d4ff 0%, #1a83dd 33%, #1a83dd 66%, #00d4ff 100%);background-size:200%, 200%;background-position:0px 0px;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.button:hover{background-position:-200px 0px}.button-hollow{width:211px;height:46px;line-height:46px;text-align:center;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;color:#f8fafd;border-radius:23px;padding:2px;box-sizing:border-box;box-shadow:1px 1px 2px #0000004a;background:linear-gradient(45deg, #00d4ff 0%, #1a83dd 33%, #1a83dd 66%, #00d4ff 100%);background-size:200%, 200%;background-position:0px 0px;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.button-hollow span{background-color:#f8fafd;border-radius:23px;height:100%;width:100%;display:flex;justify-content:center;align-items:center;color:#677897}.button-hollow:hover{background-position:-200px 0px}@-webkit-keyframes wave{0%,20%,50%,80%,100%{background-position:100% 0px}40%{background-position:100% -10px}60%{background-position:100% -5px}}@keyframes wave{0%,20%,50%,80%,100%{background-position:100% 0px}40%{background-position:100% -10px}60%{background-position:100% -5px}}

body,html,#root{height:auto;width:auto}body{background-color:#f8fafd;font-family:'Montserrat', sans-serif;font-weight:400;color:#677897}a{text-decoration:inherit;color:inherit;cursor:pointer}h1,h2,h3,h4,h5,h6{font-weight:400;margin:0}h2{font-size:36px}h3{font-size:24px}p{font-size:16px;line-height:30px}ul{list-style-type:none;margin:0;padding:0}.flex{display:flex}.flex-direction-column{flex-direction:column}.align-items-center{align-items:center}.align-items-start{align-items:flex-start}.align-items-end{align-items:flex-end}.justify-content-space-between{justify-content:space-between}.text-right{text-align:right}.text-left{text-align:left}.text-center{text-align:center}.center{margin:0 auto}.bold{font-weight:700}

